/* =Main
-------------------------------------------------------------- */

body {
	background-color: $color-white;
}

/* Site Header */
.site-title {
	margin: 0;
}

/* Site Footer */
.site-info {
	font-size: 0.75rem;
	text-align: right;
}

/* Media Queries */
@media screen and (max-width: $break-small) {

	.site-footer,
	.site-info {
		text-align: center;
	}

}
